import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-email-component",
  templateUrl: "./email-component.component.html",
  styleUrls: ["./email-component.component.css"]
})
export class EmailComponentComponent implements OnInit {
  email: string;
  operators: string[] = ["#", "X", "*"];
  selectedOperator: string;
  allowedSymbols: string =
    "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
  isAllowed(char: string): boolean {
    if (this.allowedSymbols.indexOf(char) >= 0) {
      return true;
    } else {
      return false;
    }
  }
  isAllowedEmail(char: string): boolean {
    let eAllowedSymbols: string = this.allowedSymbols + "_" + "-" + "+" + "." + '@';
    if (eAllowedSymbols.indexOf(char) >= 0) {
      return true;
    } else {
      return false;
    }
  }
  encodeEmail(str): string {
    if (this.selectedOperator === undefined) {
      this.selectedOperator = "*";
    }
    if (str === undefined) {
      return;
    }
    if (!str.includes("@")) {
      return str;
    }
    let pos: number = -1;
    let emailName;
    let domainName;
    while ((pos = str.indexOf("@", pos + 1)) != -1) {
      let tmpPos: number;
      let tmpCounter: number = 0;
      for (let i = pos; i >= 0; i--) {
        if (!this.isAllowedEmail(str[i])) {
          tmpCounter++;
        }
        if (str[i - 1] === " " || i == 0) {
          emailName = str.slice(i, pos);
          tmpPos = i;
          break;
        }
      }
      for (let i = pos; i < str.length; i++) {
        if (!this.isAllowedEmail(str[i])) {
          tmpCounter++;
        }
        if (str[i + 1] === " " || i + 1 === str.length) {
          domainName = str.slice(pos + 1, i + 1);
          break;
        }
      }
      if (
        domainName.length === 0 ||
        !domainName.includes(".") ||
        !this.isAllowed(domainName[0]) ||
        !this.isAllowed(domainName[domainName.length - 1]) ||
        tmpCounter > 0 ||
        !this.isAllowed(emailName[0]) ||
        !this.isAllowed(emailName[emailName.length - 1])
      ) {
        continue;
      }
      
      emailName = emailName.split("");
      if (emailName.length <= 2) {
        for(let i = 0; i< emailName.length; i++) {
          emailName[i] = this.selectedOperator;
        }
      } else{
        for (let i = 1; i < emailName.length - 1; i++) {
          emailName[i] = this.selectedOperator;
        }}
      str = str.split("");
      for (let i = tmpPos; i <= pos; i++) {
        for (let j of emailName) {
          str[i] = j;
          emailName.shift();
          break;
        }
      }
      str = str.join("");
    }
    return str;
  }
  constructor() {}

  ngOnInit() {}
}
