import { Component, OnInit } from "@angular/core";
import { NgModel, FormBuilder, FormGroup } from "@angular/forms";

@Component({
  selector: "app-tel-component",
  templateUrl: "./tel-component.component.html",
  styleUrls: ["./tel-component.component.css"]
})
export class TelComponentComponent implements OnInit {
  inputPhoneN: string;
  numsOfMasks: number;
  operators: string[] = ["#", "X", "*"];
  selectedOperator: string;
  numsOfMasksToF(numb: number): number {
    if (numb === undefined || numb < 0) {
      numb = 0;
    } else if (numb > 9) {
      numb = 9;
    }
    return numb;
  }
  encodePhone(str): string {
    if (this.selectedOperator === undefined) {
      this.selectedOperator = "*";
    }
    if (str === undefined) {
      return;
    }
    if (!str.includes("+")) {
      return str;
    }
    let pos: number = -1;
    let phoneNumber: string[] = [];
    while ((pos = str.indexOf("+", pos + 1)) != -1) {
      let counter: number = 0;
      let tmp = str.indexOf(" ", pos);
      let isNan: number = 0;
      for (let i = pos + 1; i < tmp; i++) {
        if (isNaN(str[i] / 2)) {
          isNan++;
        }
      }
      if (isNan > 0) {
        continue;
      }
      for (let i = tmp + 1; i < tmp + 12; i++) {
        if (typeof +str[i] === "number" && str[i] !== " ") {
          counter++;
          phoneNumber.push(str[i]);
        }
      }
      if (counter !== 9) {
        continue;
      }
      let numbOfM: number = this.numsOfMasksToF(this.numsOfMasks);
      for (
        let i = phoneNumber.length - 1;
        i >= phoneNumber.length - numbOfM;
        i--
      ) {
        phoneNumber[i] = this.selectedOperator;
      }
      str = str.split("");
      for (let i = tmp + 1; i < tmp + 12; i++) {
        for (let j of phoneNumber) {
          if (str[i] === " ") {
            i++;
          }
          str[i] = j;
          phoneNumber.shift();
          break;
        }
      }
      str = str.join("");
    }
    return str;
  }
  constructor() {}

  ngOnInit() {}
}
