import { Component, OnInit } from "@angular/core";
import { NgModel, FormBuilder, FormGroup } from "@angular/forms";
@Component({
  selector: "app-skype-component",
  templateUrl: "./skype-component.component.html",
  styleUrls: ["./skype-component.component.css"]
})
export class SkypeComponentComponent implements OnInit {
  skype: string;
  operators = ["#", "X", "*"];
  selectedOperator: string;
  allowedSymbols: string =
    "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
  isAllowed(char: string): boolean {
    if (this.allowedSymbols.indexOf(char) >= 0) {
      return true;
    } else {
      return false;
    }
  }
  encodeSkype(str): string {
    if (this.selectedOperator === undefined) {
      this.selectedOperator = "*";
    }
    if (str === undefined) {
      return;
    }
    const target: string = "skype:";
    if (!str.includes(target)) {
      return str;
    }
    let pos: number = -1;
    let skypeLogin: string;
    while ((pos = str.indexOf(target, pos + 1)) != -1) {
      str = str.split("");
      for (let i = pos + 6; i < str.length; i++) {
        if (!this.isAllowed(str[i])) {
          if (str[i] === " " || str[i] === "?") {
            skypeLogin = str.slice(pos + 7, i + 1);
            if (skypeLogin.length > 0) {
              str.splice(pos + 6, skypeLogin.length, this.selectedOperator);
              break;
            } else {
              str.splice(pos + 7, 0, " ");
              break;
            }
          } else {
            break;
          }
        }
        if (i + 1 === str.length || i + 1 > str.length) {
          skypeLogin = str.slice(pos + 6, i + 1);
          if (skypeLogin.length > 0) {
            str.splice(pos + 6, skypeLogin.length, this.selectedOperator);
            break;
          }
        }
      }
      str = str.join("");
    }
    return str;
  }
  constructor() {}
  ngOnInit() {}
}
